import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../services/authentication.service';
import {Router, Event, NavigationStart, NavigationEnd, NavigationError} from '@angular/router';

@Component({
  selector: 'app-mainback',
  templateUrl: './mainback.component.html',
  styleUrls: ['./mainback.component.css']
})
export class MainbackComponent implements OnInit {
  cobname
  constructor(private authenticationService:AuthenticationService, private router:Router,private authService:AuthenticationService) {
    router.events.subscribe( (event: Event) => {
      if (event instanceof NavigationEnd) {
      this.authService.getProfile().subscribe((userdata) => {
        this.cobname = userdata[0].user.account_name;
  
      },
      err=>{
        this.cobname = "COB Scheduling Panel - Login Page"
        return false;
      });
    }
    });
    
   }

  ngOnInit() {
 
  }

  // onLogout(){
  //   this.authenticationService.logout().subscribe(data =>{

  //     localStorage.removeItem('menuToken');
  //     this.router.navigateByUrl('/bemain/login');
  //   })
  // }

  onLogout(){
    this.authenticationService.logout();
    this.router.navigate(['/bemain/login']);
    return false;
  }

}
