import { Component, OnInit, ElementRef } from '@angular/core';
import { GetdataService } from '../../services/getdata.service';
import * as Moment from "../../../../node_modules/moment/moment.js"
declare var jquery:any;
declare var $:any;


@Component({
  selector: 'app-cobapplebyfrnt',
  templateUrl: './cobapplebyfrnt.component.html',
  styleUrls: ['./cobapplebyfrnt.component.css']
})
export class CobapplebyfrntComponent implements OnInit {

  csvgetdata=[];
  csvdatatobedisplayed=[];
  tableStyle = '0.7em';
  public event_counter:number;
  constructor(private getDataService:GetdataService) { 
    setInterval(() => {
      this.refreshData();
    }, 10000);
  }

  ngOnInit() {
   // console.log("data")
    this.event_counter=0;
  this.refreshData();
  }

  refreshData(){
    this.getDataService.getCSVData(`/csvgets/csvappleby`).map(response => response.json()).subscribe(data =>{
      this.csvgetdata = [];
      this.csvdatatobedisplayed =[];
     
      // console.log(data);

     
        for(let i=0; i<data.length; i++)
        {
          let today = new Date();
          
          let today_milliseconds = today.getTime();
          let Event_Day_Start_milliseconds = Moment(data[i].Date, 'D-MMM-YY').valueOf()
            let Event_Day_End_milliseconds = Event_Day_Start_milliseconds + 86400000;
          
           let StartTime_moment = Moment(data[i].StartTime, 'h:mmA')
           let EndTime_moment = Moment(data[i].EndTime, 'h:mmA')
            
            if(today_milliseconds > Event_Day_Start_milliseconds && today_milliseconds < Event_Day_End_milliseconds)
            {
                
              if(today.getTime() >= StartTime_moment.valueOf()-7200000 && today.getTime() < EndTime_moment.valueOf()+1800000  )
                {
                  console.log("Inserting Data")
                  this.csvgetdata.push(data[i]) ;
                }
                
                
            }
        }
         var fontChange = false;
      for(let i=this.event_counter; i< this.csvgetdata.length; i++)
         {
              
              this.csvdatatobedisplayed.push(this.csvgetdata[i]);
              
         }

      for(let i=0;  i<this.csvdatatobedisplayed.length;i++)
      {
        if(this.csvdatatobedisplayed[i].Facility == 'Community Room 1' )
        {
          this.csvdatatobedisplayed[i].Facility = "Comm Rm 1";
        }
        if(this.csvdatatobedisplayed[i].Facility == 'Community Room 2' )
        {
          this.csvdatatobedisplayed[i].Facility = "Comm Rm 2";
        }
        if(this.csvdatatobedisplayed[i].AccountName == '' )
        {
          this.csvdatatobedisplayed[i].AccountName = this.csvdatatobedisplayed[i].Subject;
        }
        if(this.csvdatatobedisplayed[i].Service =='Meeting' || this.csvdatatobedisplayed[i].Service =='Social' )
        {
          this.csvdatatobedisplayed[i].AccountName = this.csvdatatobedisplayed[i].Subject; 
        }
        if(this.csvdatatobedisplayed[i].AccountName.length>60)
        {
          
           this.tableStyle = "0.5em";
           fontChange = true;
        }
        
      }
      if(!fontChange){
        
        this.tableStyle = "0.7em";
      }

      

         this.event_counter=this.event_counter+8;

         if(this.event_counter>=this.csvgetdata.length)
         {
          this.event_counter=0;
         }else{
          // $(".table_style").fadeOut();
          // $(".table_style").fadeIn();
         }
     
    });
  }

}
