import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/authentication.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  email;
  password;
  user;
  constructor(private router:Router, private authService:AuthenticationService) {     
    if(authService.loggedIn()){
      // this.router.navigate(['/bemain/cobtansleyback'])
          this.authService.getProfile().subscribe((user) => {
            this.user=  user;
          // console.log(this.user[0].user);
          if(user.account_ID == 'cobtansley')
          {this.router.navigate(['/bemain/cobtansleyback']);
          }else if(user[0].user.account_ID == 'cobhaber'){
            this.router.navigate(['/bemain/cobhaberback']);
          }else if(user[0].user.account_ID == 'cobappleby'){
            this.router.navigate(['/bemain/cobapplebyback']);
          }else if(user[0].user.account_ID == 'cobmountainside'){
            this.router.navigate(['/bemain/cobmountainsideback']);
          }
        },
      err=>{
        ////console.log(err);
        return false;
        });
    }
   
  
  }

  ngOnInit() {
  }

  // login(){
  //   const userData = {
  //     email:this.email,
  //     password:this.password
  //   }

  //   this.authService.login(userData).subscribe(data =>{
  //     console.log(data);
  //     localStorage.setItem('menuToken', JSON.stringify(data));
  //     console.log(this.authService.loggedIn())
  //     if(this.authService.loggedIn())
  //     {
  //       console.log('logged in')
  //       this.router.navigate(['/bemain/cobtansleyback'])
  //     }
      
  //   }, err =>{
  //     alert("wrong email or password");
  //   })

  // }

  onLoginSubmit(){
    const user= {
      email: this.email,
      password: this.password
    }

    this.authService.authenticateUser(user).subscribe(data =>{
      if(data.success){
        this.authService.storeUserData(data.token, data.user);
        //    this.flashMessage.show('you are now logged in',{cssClass:'alert-success', timeout:5000});
        if(data.user.account_ID == 'cobtansley')
        {this.router.navigate(['/bemain/cobtansleyback']);
        }else if(data.user.account_ID == 'cobhaber'){
          this.router.navigate(['/bemain/cobhaberback']);
        }else if(data.user.account_ID == 'cobappleby'){
          this.router.navigate(['/bemain/cobapplebyback']);
        }else if(data.user.account_ID == 'cobmountainside'){
          this.router.navigate(['/bemain/cobmountainsideback']);
        }
        
      //  location.reload();

      }else{
        //    this.flashMessage.show(data.msg,{cssClass:'alert-danger', timeout:5000});
        this.router.navigate(['login']);
        alert("Wrong Username or Password");
      }
    });
  }
}
